import React, { Component } from 'react';
import { Tabs } from 'antd';
import VitalWarningValue from './VitalWarningValue'

export default class VitalSignSetting extends Component {
    state = {
        tabstring: "警示值設定範圍"
    }

    onChange = (activkey) => {
        this.setState({
            tabstring: activkey
        })
    }

    render() {
        const { caseinfo, ShoeVSmodel } = this.props
        const { TabPane } = Tabs;

        return (
            <Tabs defaultActiveKey={this.state.tabstring} tabPosition={'top'} onChange={this.onChange}>
                <TabPane tab="警示值設定範圍" key="警示值設定範圍">
                    <VitalWarningValue caseinfo={caseinfo} ShoeVSmodel={ShoeVSmodel}></VitalWarningValue>
                </TabPane>
                {/* <TabPane tab="趨勢圖上下限設定" key="趨勢圖上下限設定">
                    <div></div>
                </TabPane> */}
            </Tabs>
        );
    }
}
