import React, { Component } from 'react';
import { Button, DatePicker, TimePicker, InputNumber, Icon, message } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'

import VitalWarningValueRow from './VitalWarningValueRow'
import VitalWarningValueFix from './VitalWarningValueFix'
import './Vitalsign.scss'

export default class VitalWarningValue extends Component {
  state = {
    showAdd: false,
    VSRange: []
  }
  componentDidMount = () => {
    this.getVitalSignRange()
  }

  componentDidUpdate = () => {
    // this.getVitalSignRange()
  }

  getVitalSignRange = () => {
    const id = this.props.caseinfo.HISID.toString().trim()
    ApiProvider.allCenterWarningValue(id)
      .then(data => {
        this.setState({
          VSRange: data.data,
        })
      })
      .catch(err => { console.log(err) })
  }

  showAdd = () => {
    this.setState({
      showAdd: true
    })
  }

  cancelAdd = () => {
    this.setState({
      showAdd: false
    })
  }

  addRange = () => {
    const id = this.props.caseinfo.HISID.toString().trim()
    const birthday = this.props.caseinfo.PBASINGNINFO[0].birthday
    const { VSRange } = this.state
    let referData = undefined

    if (VSRange.length !== 0) {
      VSRange.sort((a, b) => {
        return b.centerwarningvaluekey.time - a.centerwarningvaluekey.time
      })
      referData = VSRange[0]
    }

    return (
      <VitalWarningValueFix id={id} referData={referData} birthday={birthday} review={this.getVitalSignRange} cancelAdd={this.cancelAdd} fixStatus={false}></VitalWarningValueFix>
    );
  }

  render() {
    const { showAdd, VSRange } = this.state
    const title = ["Date & Time", "HR", "SBP", "DBP", "RR", "SPO2", "BT"]
    const titleCellRender = title.map(function (element, index, arr) {
      return (
        <div key={index} style={{ display: "flex", alignItems: "center", borderCollapse: "collapse", paddingLeft: "10px" }}>
          {element === "SPO2" ?
            <div style={{ fontSize: "14px" }}>SpO
              <sub>2</sub>
            </div>
            :
            <div style={{ fontSize: "14px" }}>{element}</div>}
        </div>
      );
    })

    const id = this.props.caseinfo.HISID.toString().trim()
    const birthday = this.props.caseinfo.PBASINGNINFO[0].birthday

    VSRange.sort((a, b) => {
      return b.centerwarningvaluekey.time - a.centerwarningvaluekey.time
    })

    const createWarningValue = VSRange.map((element, index, arr) => {
      return (
        <VitalWarningValueRow key={index} element={element} index={index} id={id} birthday={birthday} callBack={this.getVitalSignRange}></VitalWarningValueRow>
      );
    })

    return (
      <div style={{ height: "500px" }}>
        <div style={{ display: "flex", justifyContent: "flex-end" }}>
          <Button type="primary" icon="plus" onClick={this.showAdd}>
            新增顯示值
          </Button>
        </div>
        <div style={{ marginTop: "10px" }}>
          <div style={{ height: "45px", display: "grid", gridTemplateColumns: "2fr 1fr 1fr 1fr 1fr 1fr 1fr 0.5fr", backgroundColor: "rgba(226, 237, 243,.5)" }}>
            {titleCellRender}
          </div>
          {showAdd && this.addRange()}
          <div style={{ minHeight: "120px", maxHeight: "500px", overflow: "auto" }}>
            {createWarningValue}
          </div>
        </div>
      </div>
    );
  }
}
