//建立資訊 Date=YYYY-MM-DD UpdateTime=YYYYMMDDHHmmss
export function createNotice(hisid, context, updateTime, transinno, doc, date) {
  const body = {
    "noticekey": {
      "hisid": hisid,
      "transinno": transinno,
      "updatetime": updateTime
    },
    "context": context,
    "date": date,
    "doc": doc
  }
  return axios.post("notice/createNotice", body);
}

