import React, { Component } from 'react'
import { Select } from 'antd'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'

export default class Chronicordertooltip extends Component {
    StripChange = (event) => {
        const { value, caseinfo } = this.props
        const body = {
            "orderstatuskey": {
                "caseid": caseinfo.CASEID,
                "orderseq": value.element.UDORDSEQ
            },
            "status": "false",
            "transinid": caseinfo.TRANSINID
        }
        console.log(event, body)
        if (event === "true") {
            ApiProvider.deleteOrderStatus(body)
                .then(data => {
                    this.props.DateCallback();
                })
                .catch(err => console.log(err))
        }
        if (event === "false") {
            ApiProvider.createOrderStatus(body)
                .then(data => {
                    this.props.DateCallback();
                })
                .catch(err => console.log(err))
        }
    }
    render() {
        const { value } = this.props
        const { Option } = Select;
        function statusFormat(Status) {
            if (Status === 30) {
                return "使用中"
            }
            if (Status === 60) {
                return "已完成"
            }
            if (Status === 80) {
                return "已取消"
            }
            return Status
        }
        const startTimeFormat = moment(value.element.UDBGNDT, "YYYY-MM-DD-HH:mm:ss.000000").format('MM/DD HH:mm')
        const endTimeFormat = moment(value.element.UDENDDT, "YYYY-MM-DD-HH:mm:ss.000000").format('MM/DD HH:mm')
        const status = statusFormat(Number(value.element.UDSTATUS))
        const displaystatus = value.element.status
        console.log(displaystatus, value)
        return (
            <div>
                <div>時間起迄: {`${startTimeFormat} - ${endTimeFormat}`}</div>
                <div>用法: {`${value.element.UDDOSAGE} - ${value.element.UDDUNIT} - ${value.element.UDFREQN} - ${value.element.UDROUTE}`}</div>
                <div>狀態: {status}</div>
                <div style={{ display: "flex" }}>
                    <div style={{ paddingRight: "3px" }}>備註: </div>
                    <div className="Strip">
                        <Select value={displaystatus} style={{ width: 70 }} onChange={this.StripChange}>
                            <Option value="true">顯示</Option>
                            <Option value="false">隱藏</Option>
                        </Select>
                    </div>
                </div>
            </div>
        );

    }
}