import React, { Component } from 'react'
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Nurtionfeedingcell.scss'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider';

class Nurtionfeedingcell extends Component {
    state = {
        EditMode: false,
        onEdit: false,
        powerLogin: 0,
        remark: [],
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }

    seachRemark = () => {
        const { caseinfo, data } = this.props
        ApiProvider.getOneRemark(caseinfo.TRANSINID, data.ORDSEQNO, "nurtionfeeding").then(result => {
            // console.log("getOneRemark OK " + JSON.stringify(result));
            // this.setRemark(result.data[0].remark)
            const resultitem = result.data
            if (Array.isArray(resultitem)) {
                this.setState({
                    remark: result.data,
                    onEdit: false
                })
            }
        }).catch(err => {
            // console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    onMouseEnter() {
        this.setState(
            {
                EditMode: true
            }
        )
    }
    onMouseLeave() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    showModal = () => {
        this.setState({
            onEdit: true,
        });
    };

    handleOk = newRemarkValue => {
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        this.setState({
            onEdit: false,
        });
    };
    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }
    handleRemove = e => {
        this.seachRemark()
    }

    Moduledisplay(PName, ST) {
        const remarkItem = this.state.remark

        const remarkValue = {
            "createTime": ST,
            "name": PName,
            "remarkId": this.props.data.ORDSEQNO,
            "remark": remarkItem.length === 0 ? "" : remarkItem[0]["remark"],
            "transinId": this.props.caseinfo.TRANSINID,
            "type": "nurtionfeeding",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk} onRemove={this.handleRemove}></RemarkModal>
        );
    }

    render() {
        const { data } = this.props
        const remarkItem = this.state.remark
        const startTime = moment(data.ORBGNDT.toString().trim(), "YYYY-MM-DD").format("MM/DD")
        const endTime = moment(data.ORENDDT.toString().trim(), "YYYY-MM-DD").format("MM/DD")
        const name = data.ORPROCED.toString().trim()

        return (
            <div onMouseEnter={() => this.onMouseEnter()} onMouseLeave={() => this.onMouseLeave()} className="remark" style={{ minheight: remarkItem.length === 0 ? '40px' : '60px', borderTop: '#989898 1px solid', borderBottom: "#989898 1px solid", borderCollapse: "collapse" }}>
                <div className='nurtionfeedingcell' style={{ display: "grid", gridTemplateRows: remarkItem.length === 0 ? "1fr" : "1fr 20px" }}>
                    <div className='cellforstate'>
                        <div className='celllayout'>
                            <t3>{startTime}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{endTime}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{name}</t3>
                        </div>
                    </div>
                    <div style={{ alignItems: 'center', fontWeight: 600, alignItems: "center", borderCollapse: "collapse", color: "rgb(0,120,0)", overflow: "hidden", height: "20px", display: remarkItem.length === 0 ? "none" : "flex", border: "rgba(238, 238, 238, 1) 1px solid" }}>{remarkItem.length === 0 ? "" : remarkItem[0]["remark"]}</div>
                    <div style={this.state.EditMode ? {
                        display: this.state.powerLogin == 1 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: remarkItem.length === 0 ? "40px" : "60px", background: "transparent", width: "77px"
                    }
                        :
                        { display: "none" }}>
                        <div onClick={() => this.showModal()} style={{ width: remarkItem.length === 0 ? '30px' : '40px', height: remarkItem.length === 0 ? '30px' : '40px', borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer" }} >
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                {this.Moduledisplay(name, startTime)}
            </div>
        );
    }
}

export default Nurtionfeedingcell
