import React, { Component } from 'react';
import { DatePicker, Select, InputNumber, Icon, message } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'

import './Vitalsign.scss'

export default class VitalWarningValue extends Component {
  selectTime = () => {
    const { fixStatus } = this.props
    let database = undefined
    let hour = undefined

    if (fixStatus === true) {
      database = this.props.element
      hour = moment(database.centerwarningvaluekey.time, "YYYYMMDDHHmmss").format("HH")
    }

    const selectItem = []
    const { Option } = Select;
    for (let index = 0; index < 24; index++) {
      const tempTime = index < 10 ? `0${index.toString()}` : index.toString()
      selectItem.push(<Option key={index} value={tempTime}>{tempTime}</Option >)
    }

    return (
      <Select id="selectFrom" defaultValue={hour} placeholder="請選擇時間" style={{ width: "120px" }}>
        {selectItem}
      </Select>
    )
  }

  addData = () => {
    const { fixStatus, element, id } = this.props

    const selectDate = document.querySelector('#modelDate').querySelector('.ant-calendar-picker-input').value
    const decideTime = document.querySelector('#selectFrom').querySelector('.ant-select-selection-selected-value')

    if (selectDate == '' || decideTime == null) {
      message.error('The Date is not selected', 1);
    }
    else {
      const selectTime = decideTime.innerHTML
      const saveData = {
        'centerwarningvaluekey': {
          'id': this.props.id,
          'time': moment(selectDate + selectTime, "YYYY/MM/DDHH").format("YYYYMMDDHHmmss"),
        },
        'hr_hight': document.querySelector('#hrhi').value == '' ? 160 : parseInt(document.querySelector('#hrhi').value, 10),
        'hr_low': document.querySelector('#hrlo').value == '' ? 100 : parseInt(document.querySelector('#hrlo').value, 10),
        'sbp_hight': document.querySelector('#sbphi').value == '' ? 55 : parseInt(document.querySelector('#sbphi').value, 10),
        'sbp_low': document.querySelector('#sbplo').value == '' ? 35 : parseInt(document.querySelector('#sbplo').value, 10),
        'dbp_hight': document.querySelector('#dbphi').value == '' ? 75 : parseInt(document.querySelector('#dbphi').value, 10),
        'dbp_low': document.querySelector('#dbplo').value == '' ? 55 : parseInt(document.querySelector('#dbplo').value, 10),
        'rr_hight': document.querySelector('#rrhi').value == '' ? 64 : parseInt(document.querySelector('#rrhi').value, 10),
        'rr_low': document.querySelector('#rrlo').value == '' ? 20 : parseInt(document.querySelector('#rrlo').value, 10),
        'spo2_hight': document.querySelector('#sphi').value == '' ? 100 : parseInt(document.querySelector('#sphi').value, 10),
        'spo2_low': document.querySelector('#splo').value == '' ? 90 : parseInt(document.querySelector('#splo').value, 10),
        'bt_hight': document.querySelector('#bthi').value == '' ? 38 : parseInt(document.querySelector('#bthi').value, 10),
        'bt_low': document.querySelector('#btlo').value == '' ? 36 : parseInt(document.querySelector('#btlo').value, 10),
      }

      if (saveData.hr_hight <= saveData.hr_low) {
        message.error('HR value is error', 1);
      }
      else if (saveData.sbp_hight <= saveData.sbp_low) {
        message.error('SBP value is error', 1);
      }
      else if (saveData.dbp_hight <= saveData.dbp_low) {
        message.error('DBP value is error', 1);
      }
      else if (saveData.rr_hight <= saveData.rr_low) {
        message.error('RR value is error', 1);
      }
      else if (saveData.spo2_hight <= saveData.spo2_low) {
        message.error('SPO2 value is error', 1);
      }
      else if (saveData.bt_hight <= saveData.bt_low) {
        message.error('BT value is error', 1);
      }
      else {
        ApiProvider.createCenterWarningValue(saveData)
          .then(data => {

            if (fixStatus) {
              const body = {
                'id': id,
                'time': element.centerwarningvaluekey.time
              }
              if (body.time !== saveData.centerwarningvaluekey.time) {
                ApiProvider.delectWarningValue(body)
                  .then(data => {
                    this.props.review()
                  })
                  .catch(err => { console.log(err) })
              }
              this.props.review()
              this.props.cancelFix()
            } else {
              this.props.review()
              this.props.cancelAdd()
            }
          })
          .catch(err => { console.log(err) })
      }
    }
  }

  cancelAdd = () => {
    const { fixStatus } = this.props
    if (fixStatus) {
      this.props.cancelFix()
    } else {
      this.props.cancelAdd()
    }
  }

  render() {
    const { fixStatus, birthday, referData } = this.props
    let database = undefined
    let date = undefined

    if (fixStatus === true) {
      database = this.props.element
      date = moment(database.centerwarningvaluekey.time, "YYYYMMDDHHmmss").format("YYYY/MM/DD")
    }

    const newData = {
      'hr_high': referData !== undefined ? referData.hr_hight : '',
      'hr_low': referData !== undefined ? referData.hr_low : '',
      'sbp_high': referData !== undefined ? referData.sbp_hight : '',
      'sbp_low': referData !== undefined ? referData.sbp_low : '',
      'dbp_high': referData !== undefined ? referData.dbp_hight : '',
      'dbp_low': referData !== undefined ? referData.dbp_low : '',
      'rr_high': referData !== undefined ? referData.rr_hight : '',
      'rr_low': referData !== undefined ? referData.rr_low : '',
      'spo2_high': referData !== undefined ? referData.spo2_hight : '',
      'spo2_low': referData !== undefined ? referData.spo2_low : '',
      'bt_high': referData !== undefined ? referData.bt_hight : '',
      'bt_low': referData !== undefined ? referData.bt_low : ''
    }

    return (
      <div style={{ display: "grid", gridTemplateColumns: "2fr 1fr 1fr 1fr 1fr 1fr 1fr 0.5fr", border: "1px solid rgba(232, 232, 232, 1)", borderTop: "0px", alignContent: "center", height: "60px" }}>
        <div style={{ display: "grid", gridTemplateColumns: "130px auto", alignContent: "center", fontSize: "14px", paddingLeft: "10px" }}>
          <div className="setDate" >
            {fixStatus ?
              <DatePicker id='modelDate' defaultValue={moment(date, 'YYYY/MM/DD')} disabledDate={(current) => { return current < moment(birthday, "YYYYMMDD").endOf() }} />
              :
              <DatePicker id='modelDate' disabledDate={(current) => { return current < moment(birthday, "YYYYMMDD").endOf() }} />}
          </div>
          <div className="setHour">
            {this.selectTime()}
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>High :</div>
            <div className="inputset">
              <InputNumber id="hrhi" size="small" placeholder={'160'} defaultValue={fixStatus ? database.hr_hight : newData.hr_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="hrlo" size="small" placeholder={'100'} defaultValue={fixStatus ? database.hr_low : newData.hr_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>High :</div>
            <div className="inputset">
              <InputNumber id="sbphi" size="small" placeholder={'55'} defaultValue={fixStatus ? database.sbp_hight : newData.sbp_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="sbplo" size="small" placeholder={'35'} defaultValue={fixStatus ? database.sbp_low : newData.sbp_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>High :</div>
            <div className="inputset">
              <InputNumber id="dbphi" size="small" placeholder={'75'} defaultValue={fixStatus ? database.dbp_hight : newData.dbp_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="dbplo" size="small" placeholder={'55'} defaultValue={fixStatus ? database.dbp_low : newData.dbp_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>High :</div>
            <div className="inputset">
              <InputNumber id="rrhi" size="small" placeholder={'64'} defaultValue={fixStatus ? database.rr_hight : newData.rr_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="rrlo" size="small" placeholder={'20'} defaultValue={fixStatus ? database.rr_low : newData.rr_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>High :</div>
            <div className="inputset">
              <InputNumber id="sphi" size="small" placeholder={'100'} defaultValue={fixStatus ? database.spo2_hight : newData.spo2_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="splo" size="small" placeholder={'90'} defaultValue={fixStatus ? database.spo2_low : newData.spo2_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>High :</div>
            <div className="inputset">
              <InputNumber id="bthi" size="small" placeholder={'38'} defaultValue={fixStatus ? database.bt_hight : newData.bt_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="btlo" size="small" placeholder={'36'} defaultValue={fixStatus ? database.bt_low : newData.bt_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div className="checkButton">
          <Icon type="save" style={{ fontSize: "26px" }} onClick={this.addData} />
          <Icon type="close" style={{ fontSize: "26px", marginLeft: "5px" }} onClick={this.cancelAdd} />
        </div>
      </div >
    );
  }
}
