import React, { Component } from 'react'
import * as ApiProvider from '../../API/ApiProvider'
import { Icon } from 'antd';
// import searchlogo from '../../Image/svg/Search.svg'
import Nurtionfeedingcell from './Nurtionfeedingcell'
import moment from 'moment';
import * as ApiGithub from '../../API/ApiGithub'


class Nurtionfeedingorder extends Component {
    state = {
        TRTMNTQ1: [],
        parser: { "mappings": [] },
        class: "飲食類別",

        preHID: null,
        preCID: null,
        isLoading: false
    }
    componentDidMount() {
        const { caseinfo } = this.props
        const CID = caseinfo.CASEID
        const HID = caseinfo.HISID
        this.setState({ preHID: HID, preCID: CID })
        this.getAPIFeedingData(CID, HID)
        this.getTreatmentOrderFilter()
    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const { preCID, preHID } = this.state
        const CID = caseinfo.CASEID
        const HID = caseinfo.HISID
        if (preCID !== CID || preHID !== HID) {
            this.setState({ preHID: HID, preCID: CID, isLoading: false })
            this.getAPIFeedingData(CID, HID)
        }

    }
    getAPIFeedingData(CID, HID) {
        ApiProvider.getStoredProcedureTRTMNTQ1(HID, CID)
            .then(data => { this.setState({ TRTMNTQ1: data.data, isLoading: true }) })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
    }
    getTreatmentOrderFilter() {
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Treatment");
                parser.mappings = [{ "key": "全部", "regex_str": null }, ...parser.mappings]
                this.setState({ parser: parser })
            })
            .catch(err => {
                console.log(err)
            })
    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["全部"] = []

        inputs.forEach(input => {
            let content = input.ORPROCED;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["全部"].push(input)
        });
        return allResult;
    }

    render() {
        const { caseinfo, PickerDateRange } = this.props
        const title = ["開始時間", "結束時間", "處置名稱"]
        const titleCellRender = title.map(function (element, index, arr) {
            return (
                <t3 className='title'>{element}</t3>
            );
        })

        const parse = this.parse;
        let clusteredData = {}
        if (this.state.parser !== undefined) {
            console.log(this.state.parser)
            clusteredData = parse(this.state.TRTMNTQ1, this.state.parser)
        }
        // console.log(clusteredData)
        //filter data by selected class
        let filteredData = clusteredData[this.state.class];
        let filterData = []
        if (filteredData !== undefined) {
            let ListCellData = filteredData
            filterData = ListCellData.filter((element, index, arr) => {
                const time = moment(element.ORBGNDT, 'YYYY-MM-DD').valueOf()
                return PickerDateRange[0] <= time && time <= PickerDateRange[1]
            })

            filterData = filterData.sort((a, b) => {
                let tempTimeA = moment(a.ORBGNDT, 'YYYY-MM-DD').valueOf()
                let tempTimeB = moment(b.ORBGNDT, 'YYYY-MM-DD').valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })
        }

        const DataCellRender = filterData.map(
            (element, index, arr) =>
                <Nurtionfeedingcell key={index} data={element} caseinfo={caseinfo} ></Nurtionfeedingcell>
        )

        return (
            <div>
                <div style={{ backgroundColor: "#71b0e46e", borderRadius: "4px 4px 0 0", height: "36px", padding: "0px 16px", display: "grid", gridTemplateColumns: "1fr", alignItems: "center" }
                }>
                    <div style={{ fontSize: "16px", color: "rgba(0,0,0,.7)", display: "flex", alignItems: "center" }}>Feeding Order</div>
                </div>
                <div>
                    <div className="Nurtionfeedinglayout">
                        {titleCellRender}
                    </div>
                    {
                        this.state.isLoading ?
                            <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                                <div style={{ minHeight: "120px", maxHeight: "400px", overflowY: "auto" }}>
                                    {Array.isArray(filterData) ?
                                        DataCellRender
                                        :
                                        <div style={{ display: "grid", gridTemplateColumns: "180px 1fr 140px 70px" }}>
                                            <div style={{ height: "50px", display: "flex", alignItems: "center", justifyContent: "center" }}>
                                                "當日無資料"
                                            </div>
                                        </div>
                                    }
                                </div>
                            </div>
                            :
                            (<Icon type="loading" />)
                    }
                </div>
            </div>
        );
    }
}

export default Nurtionfeedingorder
