import React, { Component } from 'react';
import { Modal, Icon, Button } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'
import VitalWarningValueFix from './VitalWarningValueFix'


import './Vitalsign.scss'

export default class VitalWarningValueRow extends Component {
  state = {
    fixData: false,
    showCheck: false
  }

  editData = () => {
    this.setState({
      fixData: true
    })
  }

  cancelData = () => {
    this.setState({
      fixData: false
    })
  }

  delectData = () => {
    this.setState({
      showCheck: true
    })
  }

  handleOk = e => {
    const parserTime = document.querySelector(`div[title="${this.props.index}"]`).querySelector('#DataDate').textContent
    const tempTime = moment(parserTime, "YYYY/MM/DD HH:mm").format("YYYYMMDDHHmmss")
    const body = {
      'id': this.props.id,
      'time': tempTime
    }

    ApiProvider.delectWarningValue(body)
      .then(data => {
        this.props.callBack()
      }, this.handleCancel())
      .catch(err => { console.log(err) })
  }

  handleCancel = e => {
    this.setState({
      showCheck: false
    });
  };

  modifyData = () => {
    const { id, element, birthday } = this.props
    return (
      <VitalWarningValueFix element={element} fixStatus={true} id={id} birthday={birthday} review={this.props.callBack} cancelFix={this.cancelData}></VitalWarningValueFix>
    )
  }

  render() {
    const { index, element } = this.props
    const { fixData } = this.state

    const date = moment(element.centerwarningvaluekey.time, "YYYYMMDDHHmmss").format("YYYY/MM/DD HH:00")

    return (
      <div>
        {fixData ? this.modifyData() :
          < div title={index} key={index} style={{ display: "grid", gridTemplateColumns: "2fr 1fr 1fr 1fr 1fr 1fr 1fr 0.5fr", border: "1px solid rgba(232, 232, 232, 1)", borderTop: "0px", alignContent: "center", height: "60px" }}>
            <div id='DataDate' style={{ display: "grid", alignContent: "center", fontSize: "14px", paddingLeft: "10px" }}>{date}</div>
            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
                <div style={{ width: "50px" }}>High :</div>
                <div >
                  {element.hr_hight}
                </div>
              </div>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
                <div>Low :</div>
                <div >
                  {element.hr_low}
                </div>
              </div>
            </div>
            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
                <div style={{ width: "50px" }}>High :</div>
                <div >
                  {element.sbp_hight}
                </div>
              </div>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
                <div>Low :</div>
                <div >
                  {element.sbp_low}
                </div>
              </div>
            </div>
            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
                <div style={{ width: "50px" }}>High :</div>
                <div >
                  {element.dbp_hight}
                </div>
              </div>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
                <div>Low :</div>
                <div >
                  {element.dbp_low}
                </div>
              </div>
            </div>
            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
                <div style={{ width: "50px" }}>High :</div>
                <div >
                  {element.rr_hight}
                </div>
              </div>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
                <div>Low :</div>
                <div >
                  {element.rr_low}
                </div>
              </div>
            </div>
            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
                <div style={{ width: "50px" }}>High :</div>
                <div >
                  {element.spo2_hight}
                </div>
              </div>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
                <div>Low :</div>
                <div >
                  {element.spo2_low}
                </div>
              </div>
            </div>
            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
                <div style={{ width: "50px" }}>High :</div>
                <div >
                  {element.bt_hight}
                </div>
              </div>
              <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
                <div>Low :</div>
                <div >
                  {element.bt_low}
                </div>
              </div>
            </div>
            <div className="checkButton">
              <Icon type="edit" style={{ fontSize: "26px" }} onClick={this.editData} />
              <Icon type="close" style={{ fontSize: "26px", marginLeft: "5px" }} onClick={this.delectData} />
            </div>
            <Modal
              visible={this.state.showCheck}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
              width={500}
              footer={null}
              centered={true}
            >
              <div style={{ display: "grid", alignContent: "center", justifyContent: "center" }}>
                <Icon style={{ fontSize: "100px" }} type="delete" />
                <div style={{ display: "flex", justifyContent: "center", width: "300px", fontSize: "20px" }}>確定要刪除此筆資料嗎？</div>
                <div style={{ display: "grid", gridTemplateColumns: "100px 100px", gridColumnGap: "5px", justifyContent: "center", width: "300px" }}>
                  <Button key="cancel" onClick={this.handleCancel}>取消</Button>
                  <Button key="ok" type="primary" onClick={this.handleOk}>確認刪除</Button>
                </div>
              </div>
            </Modal>
          </div>}
      </div>
    );
  }
}
